.popup__close-btn {
  vertical-align: bottom;
  background-color: transparent;
  border: 0;
  width: 32px;
  height: 32px;
  background-image: url('../../../images/buttons/CloseIconBig.svg');
  position: absolute;
  top: -40px;
  right: -40px;
  margin: 0;
  padding: 0;
  cursor: pointer;
}

.popup__close-btn:hover {
  opacity: .6;
}

@media (max-width: 510px) {
  .popup__close-btn {
    background-image: url('../../../images/buttons/CloseIcon.svg');
    width: 20px;
    height: 20px;
    top: -36px;
    right: 0;
  }
}
